
@media screen and (min-width: 978px) {
.panel {
    max-width: 45%;
    min-width: 45%;
    height: 330px;
}
}
@media screen and (max-width: 900px) {
.panel {
    width: 85%;
    height: 70%;
}
}
.panel {
  border-radius: 10px;
  border: thin solid #ededed;
}
.full-width {
  width: 80%;
}
.space-around {
  justify-content: space-around;
}
.input {
  border-style: none;
  border-bottom-style: solid;
  border-bottom-width: medium;
  padding: 15px;
  font-family: inherit;
  font-size: 20px;
}
.bg-color-transparent {
  background-color: transparent;
}
.color-ededed {
  color: #ededed;
}
:focus {
  border-color: #a2b70d;
}
.container {
  width: 100%;
  height: 100%;
}
.horizontal-center {
  justify-content: center;
}
.vertical-center {
  align-items: center;
}
.column {
  flex-direction: column;
}
.flex {
  display: flex;
}
.margin-10 {
  margin: 10px;
}
html,
body {
  width: 100%;
  height: 100%;
}
